@keyframes rotate-seconds {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

@keyframes rotate-minutes {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vh;
  height: 80vh;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes-hand {
  z-index: 2;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minutes;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.stopwatch__seconds-hand {
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite rotate-minutes;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite rotate-seconds;
}

* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}
